import { useEffect, useState } from 'react';
import { Button, TextField, Box, Container, Typography, TableContainer, Paper, Table, TableHead, TableRow, TableCell, TableBody, TablePagination, Link, LinearProgress } from '@mui/material';
import Page from 'src/components/Page';
import { useDispatch, useSelector } from 'src/store';
import { loadTransactions } from 'src/store/slices/slate/transaction';
import FormLabel from 'src/components/FormLabel';

const Transactions = () => {
  const [searchString, setSearchString] = useState();
  const dispatch = useDispatch();

  const { transactions, transactionsCount, transactionsPerPage, transactionsPage, loading } = useSelector((state) => state.slate.transaction);

  useEffect(() => {
    dispatch(loadTransactions({page: 0}));
  }, [dispatch]);

  const handleChangePage = (event, page) => {
    dispatch(loadTransactions({page: page}));
  };

  const handleSearch = (str) => {
    dispatch(loadTransactions({page: 0, hash: str}))
  }

  return (
    <Page title="Transactions - Block Explorer">
      <Container maxWidth="lg">
        <Box display="flex" alignItems="center">
          <Typography style={{flexGrow:1}} gutterBottom variant="h5" component="div" sx={{ mt: 2 }}>
            Latest Transactions
          </Typography>
          <Box display="flex" alignItems="center">
            <FormLabel marginBottom={0} label="Search" />
            <TextField
              fullWidth
              size="small"
              margin="dense"
              name="search"
              style={{margin: '0 12px'}}
              onChange={(e)=> setSearchString(e.target.value)}
            />
            <Button variant="contained" onClick={() => handleSearch(searchString)}>Search</Button>
          </Box>
        </Box>
        <TableContainer component={Paper} sx={{ my: 2 }}>
          {loading && <LinearProgress />}
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Block</TableCell>
                <TableCell>Transaction ID</TableCell>
                <TableCell align="right">Time</TableCell>
                <TableCell align="right"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {transactions.map((t) => (
                <TableRow key={t.id} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                  <TableCell component="th" scope="row">
                    {t.height}
                  </TableCell>
                  <TableCell>{t.id}</TableCell>
                  <TableCell align="right">{t.time}</TableCell>
                  <TableCell align="right">
                    <Link href={`/block-explorer/transactions/${t.id}`}>View</Link>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          <TablePagination rowsPerPageOptions={[]} component="div" count={transactionsCount} rowsPerPage={transactionsPerPage} page={transactionsPage} onPageChange={handleChangePage} />
        </TableContainer>
      </Container>
    </Page>
  );
};

export default Transactions;
